<template>
    <section id="topbar" class="shadow">
        <div class="container">
            <div class="row">
                <div class="col-lg-2">
                    <img :src="logo" class="logo" alt="Logo">
                </div>
                <div class="col-lg-10 text-right">
                    <div class="link-menu" v-on:click="toggleMenu()">
                        <i class="fas fa-bars"></i> SERVIÇOS ON-LINE
                    </div>
                    <div class="opcoes-usuario">
                        <img :src="imagemUsuario" alt="Imagem usuário">
                        {{ nomeUsuario }} <i class="fas fa-caret-down"></i>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>

export default {
  name: 'Topbar',
  data () {
      return {
          toggleM: false,
          toggleOpc: false,
      }
  },
  props: {
        logo: {
            type: String,
            default: require('@/assets/logoportal.png'),
        },
        nomeUsuario: {
            type: String,
            default: "",
        },
        imagemUsuario: {
            type: String,
            default: "",
        },
  },
  methods: {
      toggleMenu: function() {
          if(this.toggleM) {
                this.toggleM = false;
                $("#menu").fadeOut();
          } else {
                this.toggleM = true;
                $("#menu").fadeIn();
          }
      },
      toggleOpcoesUsuario: function() {
          if(this.toggleOpc) {
                this.toggleOpc = false;
                $("#menu").fadeOut();
          } else {
                this.toggleOpc = true;
                $("#menu").fadeIn();
          }
      },
  },
  components: {},
}
</script>

<style lang="scss">

#topbar {
    height: 80px;
    background-color: #2c2c2c;
    position: fixed;
    left: 0;
    top: 40px;
    width: 100%;
    z-index: 1050;

    .logo {
        height: 50px; 
        width: auto;
        margin-top: 15px
    }

    .link-menu {
        display: inline-block;
        margin-right: 30px;
        color: white;
        text-decoration: none;
        cursor: pointer;
    }

    .opcoes-usuario {
        color: white;
        margin-top: 20px;
        display: inline-block;
        cursor: pointer;

        img {
            width: 40px;
            height: 40px;
            border-radius: 50%;
            object-fit: cover;
            margin-right: 5px;
            object-position: top center;
        }
    }

}

</style>
