<template>
    <section id="diretorios" class="shadow">
        <div class="container">
            <div class="row">
                <div class="col-lg-12">
                    <h2><i class="far fa-folder-open"></i> {{ caminhoAtual }}</h2>
                    <button v-on:click="openItem(caminhoAnterior, true)" class="btn-out" v-if="caminhoAtual != '/ Aluno Faculdade'">
                        <span class="fa-stack">
                            <i class="fas fa-folder fa-2x"></i>
                            <i class="fas fa-arrow-up fa-stack-1x" style="color: #fff"></i>
                        </span>
                        Sair da pasta
                    </button>
                    <div class="wrap-list-diretorios row align-items-center">
                        <item-diretorio v-on:click.native="openItem(dir.nome, dir.diretorio)" v-for="dir in itens.arvore" :titulo="dir.nome" :is-dir="dir.diretorio"></item-diretorio>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>

import ItemDiretorio from '../components/ItemDiretorio.vue';

export default {
    name: 'Diretorios',
    components: {
        'item-diretorio': ItemDiretorio
    },
    data () {
        return {
            matricula: sessionStorage.getItem('matricula'), // pegando matricula da sessao
            senha: sessionStorage.getItem('senha'), // pegando senha da sessao
            itens: "",
            caminhoAnterior: "",
            caminhoAtual: "/ Aluno Faculdade",
        }
    },
    mounted: function() {
        var model = this;

        //buscar dados API
        $.ajax({    
            type: "GET",
            url: "https://api.cotemig.com.br/v1/arquivos",
            headers: {
                "Authorization": "Basic " + btoa(this.matricula + ":" + this.senha)
            },
            success: function(data) { 
                model.retrairDiretorios(data); // metodo auxiliar de atribuicao de dados às variáveis do data()
            }
        }); 
    },
    filters: {
        checkHome: function(value) {
            if(value == '/') return sessionStorage.getItem('grupo_usuario');
            
            return value;
        }
    },
    methods: {
        retrairDiretorios: function (dados) {
            this.itens = dados;
        },
        openItem: function (caminho, isDir) {
            
            if(isDir) {
                if(caminho == "/ Aluno Faculdade") {
                    caminho = "/";
                }
                
                var model = this;
                model.caminhoAnterior = model.caminhoAtual;
                model.caminhoAtual += ' / ' + caminho;


                //buscar dados API
                $.ajax({    
                    type: "GET",
                    url: "https://api.cotemig.com.br/v1/arquivos/" + caminho,
                    headers: {
                        "Authorization": "Basic " + btoa(this.matricula + ":" + this.senha)
                    },
                    success: function(data) { 
                        model.retrairDiretorios(data);
                    }
                });
            } else {
                alert("Isso é um arquivo!");
            }
        }
    }
}
</script>

<style lang="scss">

    #diretorios {
        margin-top: 25px;
        background-color: white;
        padding: 20px;
        position: relative;

        h2 {
            margin-bottom: 25px;
            font-size: 25px;
        }

        .btn-out {
            background-color: transparent;
            border: 0;
            position: absolute;
            top: 0px;
            right: 10px;
            color: #555;
            font-weight: bold;
        }

        .wrap-list-diretorios {
            background-color: #f1f1f1;
            padding: 20px;
            min-height: 400px;
            overflow-y: auto;
            overflow-x: hidden;
            max-height: 400px;
            border: 1px solid #d9d9d9;
        }

        .call-filhos {
            padding-left: 20px;
            display: none;

            &.active {
                display: block;
            }
        }
    }
</style>
