<template>
    <div class="header">

      <faixa :ultimo-acesso="puser.ultimoAcesso" v-bind:socials="[
      { link: 'https://instagram.com', icone: 'fab fa-instagram' },
      { link: 'https://facebook.com', icone: 'fab fa-facebook' },
      { link: 'https://linkedin.com', icone: 'fab fa-linkedin' },
      { link: 'https://twitter.com', icone: 'fab fa-twitter' },
      ]"></faixa>

      <topbar :logo="require('@/assets/logoportal.png')" :nomeUsuario="puser.nome" :imagem-usuario="puser.fotoPerfilURL"></topbar>

      <sidebar-menu titulo="Serviços on-line:">
        <item-sidebar-menu v-for="(p, key) in permissoes" :key="key" v-if="p" :link-menu="key | toLink" :legenda-menu="key | toLegenda"></item-sidebar-menu>
      </sidebar-menu>

    </div>
</template>

<script>

import Topbar from '../components/Topbar.vue';
import Faixa from '../components/Faixa.vue';
import SidebarMenu from '../components/SidebarMenu.vue';
import ItemSidebarMenu from '../components/ItemSidebarMenu.vue';

export default {
  name: 'Header',
  components: {
    'topbar': Topbar,
    'faixa': Faixa,
    'sidebar-menu': SidebarMenu,
    'item-sidebar-menu': ItemSidebarMenu,
  },
  data () {
    return {
      matricula: sessionStorage.getItem('matricula'), // pegando matricula da sessao
      senha: sessionStorage.getItem('senha'), // pegando senha da sessao
      puser: "", // declarando variavel do perfil do usuario
      permissoes: "", // declarando variavel de permissoes do usuario
    }
  },
  mounted: function () { // quando o component for montado
    if(sessionStorage.getItem('matricula') == null)
      window.location="/";

    var model = this;
    
    //buscar dados API
    $.ajax({
        type: "GET",
        url: "https://api.cotemig.com.br/v1/perfil",
        headers: {
          "Authorization": "Basic " + btoa(this.matricula + ":" + this.senha)
        },
        success: function(data) { 
          model.retraindoDados(data); // metodo auxiliar de atribuicao de dados às variáveis do data()
        }
    });
  },
  filters: { // traduzindo as rotas para o menu
      toLink: function (value) {
          if(!value) return '';

          var rota = '/';

          switch(value) {
            case 'arquivos':
              rota += 'area-de-arquivos';
            break;
            case 'arquivosdownload':
              rota += 'download-de-arquivos';
            break;
            case 'atualizarsenha':
              rota += 'atualizar-dados-pessoais';
            break;
            case 'aulas':
              rota += "aulas";
            break;
            case 'avisos':
              rota += "quadro-de-avisos";
            break;
            case 'boletim':
              rota += "notas-e-faltas";
            break;
            case 'boletos':
              rota += "segunda-via-boletos";
            break;
            case 'config':
              rota += "configuracao";
            break;
            case 'declaracoes':
              rota += "declaracoes";
            break;
            case 'declaracoesemitidas':
              rota += "historico-de-declaracoes";
            break;
            case 'diariodatas':
              rota += "diario-de-datas";
            break;
            case 'diariofrequencia':
              rota += "frequencia";
            break;
            case 'diarios':
              rota += "diarios";
            break;
            case 'estagios':
              rota += "estagios";
            break;
            case 'historico':
              rota += "historico";
            break;
            case 'horario':
              rota += "horario";
            break;
            case 'laboratorio':
              rota += "laboratorio";
            break;
            case 'perfil':
              rota += "perfil";
            break;
            case 'rede':
              rota += "rede";
            break;
            case 'sobre':
              rota += "sobre";
            break;
          }

          return rota;
      }, 
      toLegenda: function (value) { // traduzindo as legendas para o menu
          if(!value) return '';

          var legenda;

          switch(value) {
            case 'arquivos':
              legenda = 'Área de arquivos';
            break;
            case 'arquivosdownload':
              legenda = 'Download de arquivos';
            break;
            case 'atualizarsenha':
              legenda = 'Atualizar dados pessoais';
            break;
            case 'aulas':
              legenda = "Aulas";
            break;
            case 'avisos':
              legenda = "Quadro de avisos";
            break;
            case 'boletim':
              legenda = "Notas";
            break;
            case 'boletos':
              legenda = "2ª via de boletos";
            break;
            case 'config':
              legenda = "Configuração";
            break;
            case 'declaracoes':
              legenda = "Declarações";
            break;
            case 'declaracoesemitidas':
              legenda = "Histórico de declarações";
            break;
            case 'diariodatas':
              legenda = "Diário de datas";
            break;
            case 'diariofrequencia':
              legenda = "Frenquência";
            break;
            case 'diarios':
              legenda = "Diários";
            break;
            case 'estagios':
              legenda = "Estágio";
            break;
            case 'historico':
              legenda = "Histórico";
            break;
            case 'horario':
              legenda = "Horário";
            break;
            case 'laboratorio':
              legenda = "Laboratório";
            break;
            case 'perfil':
              legenda = "Perfil";
            break;
            case 'rede':
              legenda = "Rede";
            break;
            case 'sobre':
              legenda = "Sobre";
            break;
          }

          return legenda;
      }, 
  },
  props: {},
  methods: {
    retraindoDados: function(dados) {
      this.puser = dados; // passando perfil do usuario para variavel do data()
      this.permissoes = dados.permissoes; // passando permissoes para variavel do data()
      sessionStorage.setItem('grupo_usuario', this.puser.grupo);
    }
  },
}
</script>


<style lang="scss">

  ::-webkit-scrollbar {
      width: 5px;
  }

  ::-webkit-scrollbar-track {
      background: #2c2c2c; 
  }

  ::-webkit-scrollbar-thumb {
      background: #64b145; 
      border-radius: 10px;
  }

  ::-webkit-scrollbar-thumb:hover {
      background: #64b145; 
  }
  
</style>
