<template>
  <div id="boletim" class="shadow">
    <div class="container">
      <div class="row">
        <div class="col-lg-12">
          <h4>Notas e faltas</h4>
          <h3>{{boletimData.matricula}} - {{ boletimData.aluno }}</h3>
          <p>{{boletimData.mensagem}}</p>
          <div class="card" v-for="etapa in boletimData.etapas">
            <div class="card-header bg-dark">
              {{ etapa.nome }}
            </div>
            <div class="card-body">
              <table class="table table-bordered" v-for="disc in etapa.disciplinas">
                <thead class="thead-light">
                  <th colspan="10"><span class="badge badge-dark serie">{{ disc.serie }}</span> {{ disc.nome }}</th>
                </thead>
                <tbody>
                  <tr>
                    <th v-for="aval in disc.avaliacoes">{{ aval.nome }}</th>
                  </tr>
                  <tr>
                    <td v-for="aval in disc.avaliacoes"> Nota: <span class="nota">{{ aval.nota }}</span></td>
                  </tr>
                  <tr>
                    <td v-for="aval in disc.avaliacoes"> Valor: <span class="nota">{{ aval.valor }}</span></td>
                  </tr>
                  <tr>
                    <td v-for="aval in disc.avaliacoes"> Média: <span class="nota">{{ aval.media }}</span></td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>


export default {
  name: 'PageBoletim',
  data () {
    return {
      boletimData: "",
      matricula: sessionStorage.getItem('matricula'),
      senha: sessionStorage.getItem('senha'),
    }
  },
  mounted: function () {
    var model = this;

    //buscar dados API
    $.ajax({
      type: "GET",
      url: "https://api.cotemig.com.br/v1/boletim",
      headers: {
        "Authorization": "Basic " + btoa(this.matricula + ":" + this.senha)
      },
      success: function(data) {
        model.retraindoDados(data); // metodo auxiliar de atribuicao de dados às variáveis do data()
      }
    });
  },
  methods: {
    retraindoDados: function(dados) {
      this.boletimData = dados;
      console.log(dados);
    }
  }
}
</script>

<style lang="scss">
#boletim {
  background-color: white;
  padding: 20px;
  margin-top: 25px;
  margin-bottom: 50px;

  h4 {
    color: #777;
  }

  .card {
    margin: 50px 0;

    .card-header {
      color: white;
    }

    table {
      thead {
        .serie {
          font-size: 14px;
          font-weight: normal;
          margin-right: 20px;
        }
      }
      tr {
        td {
          span.nota {
            font-size: 20px;
            font-weight: bold;
          }
        }
      }
    }
  }
}



</style>
