<template>
    <section id="quadro_avisos">
        <div class="container">
            <div class="row">
                <aviso v-for="aviso in avisos" :key="aviso.id" :conteudo="aviso.conteudo" :titulo="aviso.titulo" :data="aviso.data" :autor="aviso.autor"></aviso>
            </div>
        </div>
    </section>
</template>

<script>

import Aviso from '../components/Aviso.vue';

export default {
  name: 'QuadroAvisos',
  data () {
        return {
            matricula: sessionStorage.getItem('matricula'), // pegando matricula da sessao
            senha: sessionStorage.getItem('senha'), // pegando senha da sessao
            avisos: '',
        }
  },
  mounted: function () { // quando o component for montado
    var model = this;

    // buscar dados API
    $.ajax({
        type: "GET",
        url: "https://api.cotemig.com.br/v1/avisos",
        headers: {
              "Authorization": "Basic " + btoa(this.matricula + ":" + this.senha)
        },
        success: function(data) { 
            model.retrairAvisos(data);
        },
        error: function(jqXHR, textStatus, errorThrown) {
            console.log(jqXHR);
        }
    }); 
  },
  methods: {
    retrairAvisos: function(dados) {
        this.avisos = dados;
        console.log(this.avisos);
    }
  },
  components: { 'aviso': Aviso },
}
</script>

<style lang="scss">
    #quadro_avisos {
        padding-top: 120px;
    }
</style>
