<template>
    <form :id="idForm" class="shadow"  v-on:submit.prevent="switchSubmit(eventoSubmit)">
      <img v-if="img" :src="img" :alt="altImg">

      <h2 v-if="titulo">{{titulo}}</h2>
            
      <div class="shadow wrap-result flipInX"><i class="fas fa-times"></i> <span id="result"></span></div>

      <slot></slot>
            
      <input type="submit" :value="valueSubmit" class="shadow">
    </form>        
</template>

<script>

export default {
    name: 'form-custom',
    props: {
        idForm: {
            type: String,
        },
        img: {
            type: String,
        },
        altImg: {
            type: String,
        },
        titulo: {
            type: String,
        },
        valueSubmit: {
            type: String,
        },
        eventoSubmit: {
            type: String,
        },
    },
    methods: {
        switchSubmit: function(form) { // metodo para verificar o submit que será acionado
            switch (form) { // este switch chama o method referente ao submit escolhido
                case "login":
                    this.validarLogin();
                break;
                default:
                    console.log("Submit não encontrado!");
                break;
            }
        },
        validarLogin: function() {
            // pegando valores dos inputs
            var matricula = $("#form_login input[name='matricula']").val(); 
            var senha = $("#form_login input[name='senha']").val();


            // requisição para a Cotemig API para validar o login
            $.ajax({
                type: "GET",
                url: "https://api.cotemig.com.br/v1/perfil",
                headers: {
                    "Authorization": "Basic " + btoa(matricula + ":" + senha)
                },
                success: function(data){ // se logar...

                    // adicionando os dados de usuário na sessao
                    sessionStorage.setItem("matricula", data.usuario);
                    sessionStorage.setItem("senha", senha);
                    sessionStorage.setItem("nome", data.nome);
                    
                    // redirecionando o usuário para dentro do portal
                    window.location="/portal";
                    
                },
                error: function(jqXHR, textStatus, errorThrown) { // se der algo errado...
                    // Insere a mensagem de erro no span de resultados
                    $("span#result").text(jqXHR.responseJSON.detalhes);
                    
                    // adiciona a classe de erro à div pai do span
                    $(".wrap-result").addClass("danger");

                    // mostra a div com um fadeIn
                    $(".wrap-result").fadeIn();
                }
            }); 
        }
    }
}
</script>

<style lang="scss">

    #form_login {
        background-color: white;
        width: 25%;
        position: relative;
        top: 50%;
        margin: 0 auto;
        transform: translateY(-50%);
        padding: 30px;

        img {
            width: 50%;
            display: block;
            margin: 0 auto 30px;   
        }

        input[type='submit'] {
            width: 100%;
            height: 50px;
            outline: none;
            padding: 0 20px;
            background-color: #64b145;
            color: white;
            font-weight: 500;
            border: 0;
            border-radius: 25px;
        }

        .wrap-result {
            width: 100%;
            margin-bottom: 20px;
            border: 0;
            display: none;
            color: white;
            padding: 15px 10px;

            &.danger {
                background-color: #cc3300;
            }

            i {
                margin-right: 10px;
            }
        }
    }
   
</style>
